css/bootstrap.min.css';\r\n\r\n/* ######################################################### */\r\n/* Global */\r\n/* ######################################################### */\r\n* {\r\n    font-family: 'Poppins', 'DM Sans', Arial, Helvetica, sans-serif;\r\n    box-sizing: border-box;\r\n    outline: 0 none transparent;\r\n    border: 0 none transparent;\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\n.shadows {\r\n    filter: drop-shadow(2px 2px 2px rgba(0,0,0,.4));\r\n}\r\n\r\n.shadows-light {\r\n    filter: drop-shadow(2px 2px 2px rgba(0,0,0,.2));\r\n}\r\n\r\n.placeholder-glow span {\r\n    border-radius: 4px;\r\n}\r\n\r\n.fw-semibold {\r\n    font-weight: 500 !important;\r\n}\r\n\r\n.modal {\r\n    z-index: 1005;\r\n}\r\n\r\n/* ######################################################### */\r\n/* Preloader */\r\n/* ######################################################### */\r\n\r\ndiv#preloader {\r\n    background-color: var(--bs-light);\r\n    z-index: 1010;\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    height: 100vh;\r\n    width: 100vw;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    transition: opacity 1s;\r\n}\r\n\r\ndiv#preloader.hide {\r\n    opacity: 0;\r\n    pointer-events: none;\r\n}\r\n\r\ndiv#preloader div {\r\n    height: 33%;\r\n    width: 100%;\r\n    max-width: 24px;\r\n    background-color: var(--bs-primary);\r\n    margin: 4px;\r\n    transform: scaleY(0);\r\n    border-radius: 4px;\r\n}\r\n\r\ndiv#preloader div:nth-of-type(1) {\r\n    animation: preloader .5s ease-out 0s infinite forwards;\r\n}\r\n\r\ndiv#preloader div:nth-of-type(2) {\r\n    animation: preloader .5s ease-out .1s infinite forwards;\r\n}\r\n\r\ndiv#preloader div:nth-of-type(3) {\r\n    animation: preloader .5s ease-out .2s infinite forwards;\r\n}\r\n\r\ndiv#preloader div:nth-of-type(4) {\r\n    animation: preloader .5s ease-out .3s infinite forwards;\r\n}\r\n\r\ndiv#preloader div:nth-of-type(5) {\r\n    animation: preloader .5s ease-out .4s infinite forwards;\r\n}\r\n\r\n@keyframes preloader {\r\n    0% {\r\n        transform: scaleY(0);\r\n    } 50% {\r\n        transform: scaleY(1);\r\n    } 100% {\r\n        transform: scaleY(0);\r\n    }\r\n}\r\n\r\n/* ######################################################### */\r\n/* Top Layer */\r\n/* ######################################################### */\r\ndiv#root {\r\n    position: relative;\r\n}\r\n\r\ndiv#map {\r\n    min-height: 100vh;\r\n    min-width: 100vw;\r\n}\r\n\r\ndiv.leaflet-control-attribution {\r\n    display: none;\r\n}\r\n\r\nsvg#crosshair {\r\n    position: absolute;\r\n    top: 50%;\r\n    left: 50%;\r\n    transform: translate(-50%,-50%);\r\n    height: 32px;\r\n    width: 32px;\r\n    z-index: 1000;\r\n    transition: opacity.3s;\r\n    pointer-events: none;\r\n}\r\n\r\nsvg#crosshair.light {\r\n    color: yellow !important;\r\n}\r\n\r\nsvg#crosshair.hide {\r\n    opacity: 0;\r\n}\r\n\r\ndiv#loader {\r\n    position: absolute;\r\n    height: 56px;\r\n    width: 56px;\r\n    top: calc(50% - 28px);\r\n    left: calc(50% - 28px);\r\n    z-index: 1004;\r\n    border: 8px solid rgba(128, 128, 128, 0.6);\r\n    border-bottom: 0 none transparent;\r\n    pointer-events: none;\r\n}\r\n\r\ndiv.controls {\r\n    position: absolute;\r\n    z-index: 1000;\r\n    transition: .3s;\r\n}\r\n\r\ndiv.controls.hide {\r\n    opacity: 0;\r\n    pointer-events: none;\r\n}\r\n\r\ndiv.controls.bottom-centre {\r\n    width: 100%;\r\n    left: 0;\r\n    bottom: 0;\r\n}\r\n\r\ndiv.controls.bottom-left {\r\n    max-width: calc(100% - 68px);\r\n    bottom: 0;\r\n    left: 0;\r\n    margin-left: 8px;\r\n    margin-bottom: 8px;\r\n    pointer-events: none;\r\n    transition: .3s;\r\n}\r\n\r\ndiv.controls.bottom-left.hide {\r\n    opacity: 0;\r\n}\r\n\r\ndiv.controls.bottom-right {\r\n    bottom: 0;\r\n    right: 0;\r\n    margin-right: 8px;\r\n    margin-bottom: 8px;\r\n}\r\n\r\ndiv.controls.bottom-right button {\r\n    height: fit-content;\r\n    width: fit-content;\r\n    display: block;\r\n}\r\n\r\ndiv.controls.bottom-right > button {\r\n    border: 2px solid var(--bs-gray-500) !important;\r\n}\r\n\r\ndiv.controls.bottom-right svg {\r\n    height: 24px;\r\n    width: 24px;\r\n}\r\n\r\ndiv.controls.top {\r\n    width: 100%;\r\n    max-width: 456px;\r\n    top: 0;\r\n    left: 0;\r\n    padding: 8px 8px 0;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    z-index: 1001;\r\n}\r\n\r\ndiv.controls.top.show {\r\n    opacity: 1 !important;\r\n}\r\n\r\ndiv.controls.top > .left {\r\n    width: 100%;\r\n}\r\n\r\n/* ######################################################### */\r\n/* Base Layer Controls */\r\n/* ######################################################### */\r\ndiv#base-layer-control {\r\n    width: 44px;\r\n    margin-bottom: 16px;\r\n}\r\n\r\ndiv#base-layer-control .btn {\r\n    box-sizing: border-box;\r\n    padding: 0;\r\n    overflow: hidden;\r\n    border: 2px solid var(--bs-gray-500) !important;\r\n}\r\n\r\ndiv#base-layer-control label.btn {\r\n    display: block;\r\n    border-radius: 50%;\r\n    margin-bottom: 8px;\r\n    width: fit-content;\r\n}\r\n\r\ndiv#base-layer-control button.btn {\r\n    width: 100%;\r\n    border-radius: 8px;\r\n}\r\n\r\ndiv#base-layer-control input {\r\n    display: none;\r\n}\r\n\r\ndiv#base-layer-control img {\r\n    height: auto;\r\n    width: 100%;\r\n}\r\n\r\n/* ######################################################### */\r\n/* Zoom Controls /*\r\n/* ######################################################### */\r\ndiv#zoom-control {\r\n    margin-top: 8px;\r\n    overflow: hidden;\r\n    border-radius: 16px;\r\n    border: 2px solid var(--bs-gray-500) !important;\r\n}\r\n\r\ndiv#zoom-control .btn {\r\n    padding: 8px;\r\n    overflow: hidden;\r\n    border: 0 none transparent !important;\r\n}\r\n\r\ndiv#zoom-control .btn-light {\r\n    color: var(--bs-gray-700);\r\n}\r\n\r\ndiv#zoom-control .btn-light:hover {\r\n    background-color: var(--bs-gray-200);\r\n    color: var(--bs-gray-800);\r\n}\r\n\r\n/* ######################################################### */\r\n/* Other Buttons /*\r\n/* ######################################################### */\r\n\r\nbutton.round {\r\n    padding: 8px;\r\n    margin-bottom: 8px;\r\n    border-radius: 50%;\r\n}\r\n\r\nbutton.active {\r\n    outline: 2px solid var(--bs-primary) !important;\r\n    color: var(--bs-primary) !important;\r\n}\r\n\r\nbutton.btn-secondary.active {\r\n    outline: 2px solid var(--bs-primary) !important;\r\n    background-color: var(--bs-primary);\r\n    color: white !important;\r\n}\r\n\r\n/* ######################################################### */\r\n/* Search Bar /*\r\n/* ######################################################### */\r\n\r\ninput#q {\r\n    flex: 2;\r\n    max-width: 100%;\r\n    transition: .5s;\r\n}\r\n\r\ninput#q.show {\r\n    max-width: 100%;\r\n    padding: 6px 12px;\r\n}\r\n\r\ninput#q:focus {\r\n    max-width: 100%;\r\n    padding: 6px 12px;\r\n}\r\n\r\nselect#country {\r\n    flex: 1;\r\n    font-size: .8rem;\r\n    width: fit-content;\r\n    max-width: 100%;\r\n    overflow: hidden;\r\n    transition: .5s;\r\n}\r\n\r\ndiv#search-bar {\r\n    position: relative;\r\n    z-index: 2;\r\n}\r\n\r\ndiv#search-results {\r\n    z-index: 1;\r\n    position: relative;\r\n}\r\n\r\ndiv#search-results button {\r\n    font-size: .7rem;\r\n}\r\n\r\nbutton.list-group-item.active {\r\n    color: #fff !important;\r\n}\r\n\r\n@media screen and (max-width: 440px) {\r\n    input#q {\r\n        max-width: 0;\r\n        padding: 6px 0;\r\n    }\r\n\r\n    select#country.hide {\r\n        max-width: 0;\r\n        padding-left: 0;\r\n        padding-right: 0;\r\n        border-left: 0;\r\n        border-right: 0;\r\n    }\r\n}\r\n\r\n/* ######################################################### */\r\n/* Toast /*\r\n/* ######################################################### */\r\n\r\n.toast-container {\r\n    width: 100% !important;\r\n    max-width: 440px !important;\r\n}\r\n\r\n.toast-container img {\r\n    height: 20px;\r\n    width: auto;\r\n}\r\n\r\n.toast-container div.toast {\r\n    min-width: 100%;\r\n    box-shadow: 0 0 0 transparent;\r\n}\r\n\r\n/* ######################################################### */\r\n/* Bottom left Address Details /*\r\n/* ######################################################### */\r\n\r\ndiv.controls.bottom-left .alert {\r\n    min-height: 58px;\r\n    margin: 0;\r\n    background-color: rgba(248,249,250,.6);\r\n    color: var(--bs-gray-900);\r\n    backdrop-filter: blur(4px);\r\n    transition: .3s;\r\n    padding: 8px 16px;\r\n    border-radius: 16px;\r\n}\r\n\r\ndiv.controls.bottom-left .alert.show {\r\n    width: 100%;\r\n}\r\n\r\ndiv.controls.bottom-left .alert:hover {\r\n    background-color: rgba(248,249,250,.8);\r\n}\r\n\r\ndiv.controls.bottom-left .alert svg {\r\n    min-width: 24px;\r\n}\r\n\r\ndiv.controls.bottom-left .alert > div {\r\n    margin-left: 16px;\r\n    font-weight: 500;\r\n    font-size: .8rem;\r\n}\r\n\r\ndiv.controls.bottom-left .alert .placeholders {\r\n    width: 100vw;\r\n    max-width: 320px;\r\n}\r\n\r\ndiv.controls.bottom-left .alert .placeholder-glow {\r\n    display: block;\r\n    height: .8rem;\r\n    margin-bottom: .4rem;\r\n    width: 100%;\r\n}\r\n\r\ndiv.controls.bottom-left .alert .placeholder-glow:last-of-type {\r\n    width: 75%;\r\n}\r\n\r\n/* ######################################################### */\r\n/* Bottom left Weather Info /*\r\n/* ######################################################### */\r\n\r\ndiv#weather-container {\r\n    background-color: rgba(248,249,250,.6);\r\n    backdrop-filter: blur(4px);\r\n    border-radius: 16px !important;\r\n    width: fit-content;\r\n}\r\n\r\ndiv#weather-container:hover {\r\n    background-color: rgba(248,249,250,.8);\r\n}\r\n\r\ndiv#weather-container span {\r\n    line-height: 1;\r\n}\r\n\r\ndiv#weather-container img {\r\n    width: 50px;\r\n    height: 50px;\r\n}\r\n\r\ndiv#weather-container .placeholder-glow {\r\n    width: 50px;\r\n    height: 50px;\r\n    padding: 8px\r\n}\r\n\r\ndiv#weather-container .placeholder-glow span {\r\n    height: 100%;\r\n    width: 100%;\r\n}\r\n\r\n/* ######################################################### */\r\n/* More Info /*\r\n/* ######################################################### */\r\n\r\ndiv#more-info-menu {\r\n    max-width: 456px;\r\n    width: 100%;\r\n    position: absolute;\r\n    bottom: 0;\r\n    left: 0;\r\n    background-color: rgba(248,249,250,.6);\r\n    backdrop-filter: blur(4px);\r\n    z-index: 1000;\r\n    transition: .5s;\r\n}\r\n\r\ndiv#more-info-menu:hover {\r\n    background-color: rgba(248,249,250,.8);\r\n}\r\n\r\ndiv#more-info-menu.show {\r\n    display: flex;\r\n}\r\n\r\ndiv#more-info-menu.collapsing {\r\n    display: flex;\r\n}\r\n\r\ndiv#more-info-menu #info {\r\n    position: relative;\r\n    padding-top: 54px;\r\n    height: 100vh;\r\n    width: 100%;\r\n    display: flex;\r\n    flex-direction: column;\r\n}\r\n\r\ndiv#more-info-menu header {\r\n    font-weight: 500 !important;\r\n}\r\n\r\ndiv#more-info-menu header h1 {\r\n    display: inline-block;\r\n    word-wrap: break-word;\r\n}\r\n\r\ndiv#more-info-menu div.modal-body {\r\n    height: calc(100% - 117px);\r\n    flex: 1 1;\r\n    overflow-y: auto;\r\n}\r\n\r\ndiv#more-info-menu div.modal-body::-webkit-scrollbar {\r\n    width: 8px;\r\n}\r\n\r\ndiv#more-info-menu div.modal-body::-webkit-scrollbar-thumb {\r\n    background: rgba(0,0,0,.1);\r\n    border-radius: 4px;\r\n}\r\n\r\ndiv#more-info-menu div.modal-body::-webkit-scrollbar-thumb:hover {\r\n    background: rgba(0,0,0,.15);\r\n}\r\n\r\ndiv#more-info-menu p {\r\n    font-size: .8rem;\r\n}\r\n\r\ndiv#more-info-menu .weather {\r\n    width: 100%;\r\n    margin: 0 auto;\r\n    background-color: rgba(0, 0, 0, 0.05);\r\n    border-radius: 16px;\r\n    overflow: hidden;\r\n    box-shadow: inset 4px 4px 4px rgba(0, 0, 0, 0.1);\r\n    min-height: 176px;\r\n}\r\n\r\ndiv#more-info-menu .weather img {\r\n    width: 100%;\r\n    height: auto;\r\n    margin: -8px 0 -16px;\r\n}\r\n\r\ndiv#more-info-menu .weather .day {\r\n    font-weight: 500;\r\n}\r\n\r\ndiv#more-info-menu .weather .temp {\r\n    font-size: 1.6rem\r\n}\r\n\r\ndiv#more-info-menu .weather .temp sup {\r\n    font-size: 1rem;\r\n}\r\n\r\ndiv#more-info-menu .weather .left {\r\n    flex: 1 0 33%;\r\n    text-align: center;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: space-between;\r\n    background-color: rgba(0, 0, 0, 0.1);\r\n    padding: 16px 0 16px 0;\r\n}\r\n\r\ndiv#more-info-menu .weather .left img {\r\n    margin: -20px 0 -40px;\r\n}\r\n\r\ndiv#more-info-menu .weather .right {\r\n    flex: 2 2 66%;\r\n    display: flex;\r\n    flex-direction: column;\r\n    padding-top: 8px;\r\n}\r\n\r\ndiv#more-info-menu .weather .right .top {\r\n    flex: 2;\r\n}\r\n\r\ndiv#more-info-menu .weather .right .top div {\r\n    flex: 1 1;\r\n    text-align: center;\r\n    margin: auto;\r\n}\r\n\r\ndiv#more-info-menu .weather .right .bottom {\r\n    display: flex;\r\n    flex-wrap: wrap;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    background-color: rgba(0, 0, 0, 0.1);\r\n    padding: 4px 0;\r\n    row-gap: 0px;\r\n    height: fit-content;\r\n}\r\n\r\ndiv#more-info-menu .weather .right .bottom p {\r\n    font-weight: 500;\r\n    margin-bottom: 0;\r\n    margin-right: 8px;\r\n}\r\n\r\ndiv#more-info-menu #business-info {\r\n    background-color: var(--bs-light);\r\n    border-radius: 16px;\r\n}\r\n\r\ndiv#more-info-menu #business-info img {\r\n    max-height: 232px;\r\n    object-fit: cover;\r\n    object-position: center;\r\n}\r\n\r\ndiv#more-info-menu #business-info h2 {\r\n    position: relative;\r\n    z-index: 5;\r\n}\r\n\r\ndiv#more-info-menu #business-info .card-icons {\r\n    position: absolute;\r\n    top: 0;\r\n    right: 0;\r\n    display: flex;\r\n    margin: 8px;\r\n    z-index: 2;\r\n}\r\n\r\ndiv#more-info-menu #business-info .card-icons > div {\r\n    border-radius: 50%;\r\n    margin-left: 8px;\r\n    height: 48px;\r\n    width: 48px;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\ndiv#more-info-menu #business-info .card-icons > div > svg {\r\n    border-radius: 50%;\r\n    fill: white;\r\n    height: 60%;\r\n    width: 60%;\r\n}\r\n\r\ndiv#more-info-menu #business-info .rating {\r\n    overflow: hidden !important;\r\n    white-space: nowrap;\r\n}\r\n\r\ndiv#more-info-menu #business-info .rating > svg {\r\n    fill: #FFD700;\r\n    stroke: transparent;\r\n    height: 16px !important;\r\n    width: 16px !important;\r\n}\r\n\r\n/* ######################################################### */\r\n/* Exchange Rates /*\r\n/* ######################################################### */\r\n\r\ndiv.select-rates input {\r\n    width: 88px;\r\n    background-color: transparent;\r\n    font-weight: 500;\r\n}\r\n\r\n/* ######################################################### */\r\n/* Starting Modal /*\r\n/* ######################################################### */\r\n\r\ndiv.location .modal-body {\r\n    font-size: .9rem;\r\n}\r\n\r\ndiv.modal-backdrop {\r\n    z-index: 1000;\r\n}\r\n\r\ndiv.location .modal-footer svg {\r\n    height: 24px;\r\n    width: 24px;\r\n}\r\n\r\n/* ######################################################### */\r\n/* Wiki Carousel /*\r\n/* ######################################################### */\r\n\r\ndiv#wiki {\r\n    margin-bottom: -16px;\r\n}\r\n\r\ndiv#wiki .card,\r\ndiv#news .card {\r\n    border-radius: 16px !important;\r\n}\r\n\r\ndiv#news .card img {\r\n    width: 100%;\r\n    height: 100%;\r\n    aspect-ratio: 16 / 9;\r\n    object-fit: cover;\r\n}\r\n\r\ndiv#wiki .card small, div#wiki .card h5 {\r\n    display: block;\r\n    text-overflow: ellipsis;\r\n    overflow: hidden;\r\n    white-space: nowrap;\r\n}\r\n\r\ndiv#wiki .card p:nth-of-type(1),\r\ndiv#news .card p:nth-of-type(1),\r\ndi